html {
	height: 100%;
}
* {
	box-sizing: border-box;
}
body {
	margin: 0;
	padding: 0;
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans",
		"Droid Sans", "Helvetica Neue", sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background: url(/facify/static/media/main_background.1548f2fe.svg);
	background-size: cover;
	background-repeat: no-repeat;
	text-align: center;
}

code {
	font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.App {
	max-width: 800px;
	padding: 0 20px;
	margin: auto;
}

main {
	background: #ffffff;
	margin-bottom: 50px;
}

h1 {
	color: hsl(0, 0%, 57%);
	font-size: 1rem;
}

.input-button {
	border: none;
	padding: 10px 20px;
	color: #d1d1d1;
	position: relative;
	background: #000066;
	cursor: pointer;
}

.input-button:hover {
	color: white;
	background: #00004e;
}

.input-button:focus {
	outline: none;
	color: white;
	background: #000000;
}

.input-text {
	padding: 10px;
	background: #ffffff;
	border: none;
	color: #8e8e8e;
	font-weight: normal;
}

.input-text:focus {
	outline: 5px auto #000066;
	color: #333333;
	font-weight: bold;
}

.input-group {
	display: grid;
	grid-template-columns: 1fr;
}

#logo {
	color: white;
	font-weight: bold;
	font-size: 2rem;
	margin: 50px 0;
}

.bounding-box {
	position: absolute;
	box-shadow: inset 0 0 0 1px #14bff2;
	border: 0.9px solid #020259;
	z-index: 1;
}

.container {
	position: relative;
}

.image-demo {
	width: 100%;
}

.image-panel {
	display: grid;
	grid-template-columns: 1fr;
	grid-template-rows: auto;
	margin: 0 auto;
}

.info-demo {
	display: grid;
	grid-template-columns: repeat(4, 1fr);
	justify-items: center;
	grid-row-gap: 10px;
	overflow-y: auto;
	height: 160px;
}

canvas {
	display: block;
}

